 * @LastEditTime: 2022-03-04 14:04:59
 -->
<template>
  <div>
    <el-col :span="24">
      <vxe-grid border="full"
                resizable
                keep-source
                ref="xGrid"
                class="fix-mg-table"
                :edit-rules="validRules"
                :columns="tableColumns"
                :data="tableDatas"
                height="550"
                :edit-config="{trigger: 'click', mode: 'row', showStatus: true, icon: 'fa fa-file-text-o'}"></vxe-grid>
    </el-col>
    <!-- <el-col :span="24"
            class="actions">
      <el-button type="primary"
                 @click="submit">{{BTN_TEXT.SAVE}}</el-button>
      <el-button @click="closeAction">{{BTN_TEXT.RETURN}}</el-button>
    </el-col> -->
  </div>
</template>
<script>
// API
import { insertOrUpdateBatchTool } from '@/api/equipment.js';
import {
  extendPropertyGetListAction,
  extendPropertyValueInsertOrUpdateBatchAction
} from '@/api/extendConfig';
import { EQUIPMENT_DATA, STATUS_CODE, TITLE, MSG, VARIABLE, BTN_TEXT, CONSTANTS_DEVICE_CONFIGURATION, FORM_CONFIG_DATE } from '@/utils/constantsFile';
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import moment from 'moment';
export default {
  components: {
  },
  props: {
    addFlag: { type: Number, default: 0 },
    code: { type: String, default: '' },
    handleClose: Function
  },
  data() {
    // 扩展字段通用验证
    const setValidateForm = ({ column, row, cellValue }) => {
      return new Promise((resolve, reject) => {
        return this.$options.filters.formatterTableValidate(column, cellValue, this.extendPropertyVoList, resolve, reject);
      });
    };
    const validateAcquaintance = ({ cellValue }) => {
      return new Promise((resolve, reject) => {
        if (cellValue < 0) {
          reject(new Error(MSG.CONTENT_LIMITIATION));
        } else {
          resolve();
        }
      });
    };
    return {
      isDurationUnit: false,
      setValidateForm: setValidateForm,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      FORM_CONFIG_DATE: FORM_CONFIG_DATE,
      VARIABLE: VARIABLE,
      BTN_TEXT: BTN_TEXT,
      CONSTANTS_DEVICE_CONFIGURATION: CONSTANTS_DEVICE_CONFIGURATION,
      imageUrl: '',
      // 页面标题
      titleName: CONSTANTS_DEVICE_CONFIGURATION.ADD_WARN,
      // 表单验证
      validRules: {
      },
      // 原始数据
      OldRules: {
        toolTypeId:
          [
            { required: true, message: MSG.PLEASE_ENTER_CONTENT }
          ],
        toolName: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { min: 1, max: 20, message: MSG.CONTENT_LIMITIATION }
        ],
        // toolCode: [
        //   { required: true, message: MSG.PLEASE_ENTER_CONTENT },
        //   { min: 1, max: 20, message: MSG.CONTENT_LIMITIATION }
        // ],
        // toolModel: [
        //   { required: true, message: MSG.PLEASE_ENTER_CONTENT },
        //   { min: 1, max: 20, message: MSG.CONTENT_LIMITIATION }
        // ],
        initialLife: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { min: 0, max: 6, message: MSG.CONTENT_LIMITIATION },
          { validator: validateAcquaintance }
        ],
        toolMaxLife: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { validator: validateAcquaintance },
          { min: 1, max: 6, message: MSG.CONTENT_LIMITIATION }
        ]
        // manufacturer: [
        //   { required: true, message: MSG.PLEASE_ENTER_CONTENT },
        //   { min: 1, max: 20, message: MSG.CONTENT_LIMITIATION }
        // ]
      },
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [],
      // 原始数据
      tableColumns_old:
        [
          {
            field: 'toolTypeId',
            title: EQUIPMENT_DATA.TOOL_TYPE,
            minWidth: '100',
            fixed: 'left',
            align: 'center',
            editRender: {
              name: '$select',
              options: this.$store.state.filters.toolTypeList2
            }
          },
          {
            field: 'toolName',
            title: EQUIPMENT_DATA.TOOL_NAME,
            minWidth: '100',
            align: 'center',
            editRender: { name: 'input', attrs: { type: 'text' } }
          },
          {
            field: 'toolCode',
            title: EQUIPMENT_DATA.TOOL_CODE,
            minWidth: '100',
            align: 'center',
            editRender: { name: 'input', attrs: { type: 'text' } }
          },
          {
            field: 'toolModel',
            title: EQUIPMENT_DATA.TOOL_TYPE_CODE,
            minWidth: '100',
            align: 'center',
            editRender: { name: 'input', attrs: { type: 'text' } }
          },
          {
            field: 'initialLife',
            title: EQUIPMENT_DATA.SET_TOOL_START_AGE,
            minWidth: '100',
            align: 'center',
            editRender: { autofocus: '.my-input' },
            slots: {
              edit: ({ row }) => {
                return [
                  <el-input min="0" max="999999" type="number" class="my-input" v-model={row.initialLife} />
                ];
              }
            }
          },
          {
            field: 'toolMaxLife',
            title: EQUIPMENT_DATA.SET_TOOL_MAX_AGE,
            minWidth: '100',
            align: 'center',
            editRender: { autofocus: '.my-input' },
            slots: {
              edit: ({ row }) => {
                return [
                  <el-input min="0" max="999999" type="number" class="my-input" v-model={row.toolMaxLife} />
                ];
              }
            }
          },
          {
            field: 'manufacturer',
            title: EQUIPMENT_DATA.TOOL_FACTORY,
            minWidth: '100',
            align: 'center',
            editRender: { name: 'input', attrs: { type: 'text' } }
          },
          {
            field: 'recordType',
            title: EQUIPMENT_DATA.TIME_WAY,
            minWidth: '100',
            align: 'center',
            editRender: {
              name: '$select',
              options: [
                {
                  value: '1',
                  label: this.$store.state.pageInfo.pageConfigInfo.yieldKey + '计数'
                },
                {
                  value: '2',
                  label: '时间计数'
                }
              ]
              // events: {
              //   change: (row, select) => {
              //     if (select.value === '2') {
              //       this.$refs.xGrid.showColumn(this.$refs.xGrid.getColumnByField('timeRecordUnit'));
              //     } else {
              //       this.$refs.xGrid.hideColumn(this.$refs.xGrid.getColumnByField('timeRecordUnit'));
              //     }
              //   }
              // }
            }
          },
          {
            // visible: false,
            field: 'timeRecordUnit',
            title: EQUIPMENT_DATA.DURATION_UNIT,
            minWidth: '100',
            align: 'center',
            editRender: {
              name: 'select',
              options: [
                {
                  value: null,
                  label: ''
                },
                {
                  value: 1,
                  label: '秒'
                },
                {
                  value: 2,
                  label: '分'
                },
                {
                  value: 3,
                  label: '时'
                }
              ]
            }
          },
          {
            field: '',
            title: BTN_TEXT.OPERATION,
            width: '100',
            fixed: 'right',
            align: 'center',
            slots: {
              default: ({ row, column }) => {
                var buttonList = [
                  <el-button onClick={() => this.deleteAddTool(row)} type="text">
                    {BTN_TEXT.DELETE}
                  </el-button>
                ];
                return buttonList;
              },
              header: ({ column }) => {
                return [
                  <span>
                    <span>{column.title}</span>
                  </span>
                ];
              }
            }
          }
        ],
      // 告警数据
      deviceWarningAutoOptions: [{
        value: 1,
        label: VARIABLE.YES
      }, {
        value: 0,
        label: VARIABLE.NO
      }],
      // 扩展字段数据集
      extendPropertyVoList: []
    };
  },
  computed: {},
  created() {

  },
  mounted() {
    this.initExtendTable();
  },
  watch: {
    addFlag(val, oldVal) {
      this.insertEvent();
    }
  },
  methods: {
    /**
     * @description: 查询页面的扩展字段
     * @return: 无
     * @author: ZJM
     */
    async initExtendTable() {
      try {
        const res = await extendPropertyGetListAction(
          {},
          {
            conditionJson1: [
              {
                column: 'extendType',
                conditionJson1: [],
                conditionJson2: [],
                current: 0,
                operator: 1,
                operatorGroup: 0,
                orderBy: {},
                ptype: 'int',
                size: 0,
                type: 'eq',
                value: 5
              }
            ]
          });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.extendPropertyVoList = res.rows;
          this.setinitExtend(res.rows);
          this.extendRules(res.rows);
          this.insertEvent();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    getToolTypeNameFilter() {
      if (this.$store.state.filters.toolTypeList) {
        var column = this.$refs.addTable.getColumnByField('toolTypeId');
        var array = [];
        this.$store.state.filters.toolTypeList.forEach(function(item) {
          array.push({
            value: item.basicId,
            label: item.basicValue
          });
        });
        column.editRender.options = array;
      }
    },
    /**
     * @description: 移除一行
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    deleteAddTool(data) {
      this.$XModal.confirm('您确定要删除该数据?').then(type => {
        if (type === 'confirm') {
          var removeIdx = null;
          for (var i = 0; i < this.tableDatas.length; i++) {
            if (this.tableDatas[i]._XID === data._XID) {
              removeIdx = i;
              break;
            }
          }
          this.tableDatas.splice(removeIdx, 1);
        }
      });
    },
    /**
     * @description: 执行新增一行
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    async insertEvent() {
      const record = {
        toolTypeId: null,
        toolTypeName: null,
        toolName: null,
        toolCode: null,
        toolModel: null,
        initialLife: 0,
        toolMaxLife: null,
        manufacturer: null,
        deviceCode: this.code
      };
      for (var item in this.extendPropertyVoList) {
        // if(this.extendPropertyVoList[item].propertyType===1){
        record[this.extendPropertyVoList[item].propertyCode] = '';
        // } else
      }
      // this.tableDatas.push(record);
      // 往表格插入临时数据，从指定位置插入一行或多行
      this.tableDatas.push(record);
    },
    /**
     * @description: 表单提交
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async submit() {
      var self = this;
      var msgList = [];
      const errMap = await self.$refs.xGrid
        .fullValidate()
        .catch(errMap => errMap);
      if (!errMap) {
        self.tableDatas.forEach(function(item, idx) {
          var index = idx + 1;
          if (!item.toolTypeId) {
            msgList.push('第 ' + index + ' 行 工具名称 校验未通过！：请输入内容');
          }
          if (item.initialLife < 0) {
            msgList.push('第 ' + index + ' 行 初始寿命 校验未通过！：不能为负数');
          }
          if (item.toolMaxLife < 0) {
            msgList.push('第 ' + index + ' 行 最大寿命 校验未通过！：不能为负数');
          }
        });
      } else {
        Object.values(errMap).forEach(errList => {
          errList.forEach((params, index) => {
            const { column, rules } = params;
            rules.forEach(rule => {
              msgList.push(
                `${VARIABLE.THE} ${index + 1} ${VARIABLE.ROW} ${column.title} ${MSG.VALIDATION_FAILED}：${rule.message}`
              );
            });
          });
        });
      }
      if (msgList.length > 0) {
        self.$XModal.alert({
          status: 'error',
          message: () => {
            return [
              <div class="red" style="max-height: 400px;overflow: auto;">
                {msgList.map(msg => (
                  <div>{msg}</div>
                ))}
              </div>
            ];
          }
        });
      } else {
        // 通过验证，执行提交
        this.saveAddToolData();
      }
    },
    /**
     * @description: 保存添加的工具
     * @return: void
     */
    saveAddToolData() {
      if (this.tableDatas[0].timeRecordUnit) {
        this.tableDatas.forEach(item => {
          if (item.timeRecordUnit * 1 === 2) {
            item.initialLife = item.initialLife * 60;
            item.toolMaxLif = item.toolMaxLif * 60;
          } else if (item.timeRecordUnit === 3) {
            item.initialLife = item.initialLife * 3600;
            item.toolMaxLif = item.toolMaxLif * 3600;
          }
        });
      }
      insertOrUpdateBatchTool(null, this.tableDatas).then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          if (this.extendPropertyVoList.length > 0) {
            this.saveExtend(res.rows, this.tableDatas);
          }
          this.closeAction();
          showSuccessMessage(res.message);
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(err);
      });
    },
    /**
     * @description: 选择日期显示值
     * @param value 选择值
     * @param format 日期格式化
     * @return: 显示值
     * @author: ZJM
     */
    formatDate(value, format) {
      if (!value) {
        return '';
      } else {
        return moment(value).format(format);
      }
    },
    /**
     * @description: 选择下拉显示值
     * @param value 选中值
     * @param list 列表值
     * @return:显示值
     * @author: ZJM
     */
    getSelectLabel(value, list, valueProp = 'value', labelField = 'label') {
      const item = this.$utils.find(list, item => item[valueProp] === value);
      return item ? item[labelField] : null;
    },
    /**
     * @description: 返回按钮点击
     * @param 无
     * @return:无
     * @author: ZJM
     */
    closeAction() {
      if (this.handleClose) {
        this.handleClose();
      }
    },
    /**
     * @description: 保存表头扩展字段数据
     * @return: 无
     * @author: ZJM
     */
    saveExtend(row, record) {
      var arr = [];
      for (var x in row) {
        for (var y in this.extendPropertyVoList) {
          var json = {
            extendId: this.extendPropertyVoList[y].extendId,
            extendValue: record[x][this.extendPropertyVoList[y].propertyCode],
            propertyCode: this.extendPropertyVoList[y].propertyCode,
            relationshipTypeId: row[x].toolId
          };
          arr.push(json);
        }
      }
      this.setSaveExtend(arr);
    },
    /**
     * @description: 执行扩展字段保存接口
     * @return: 无
     * @author: ZJM
     */
    async setSaveExtend(arr) {
      try {
        const res = await extendPropertyValueInsertOrUpdateBatchAction({}, arr);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.closeAction();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行扩展字段验证遍历
     * @return: 无
     * @author: ZJM
     */
    extendRules(row) {
      this.validRules = JSON.parse(JSON.stringify(this.OldRules));
      for (var item in this.extendPropertyVoList) {
        if (this.extendPropertyVoList[item].propertyIsNull === 1) {
          // 若类型是日期
          if (this.extendPropertyVoList[item].propertyType === 3) {
            this.validRules[this.extendPropertyVoList[item].propertyCode] = [
              { required: true, message: MSG.PLEASE_ENTER_CONTENT },
              { validator: this.setValidateForm }
            ];
          } else { // 若类型是字符或数字
            this.validRules[this.extendPropertyVoList[item].propertyCode] = [
              { required: true, message: MSG.PLEASE_ENTER_CONTENT },
              { validator: this.setValidateForm }
            ];
          }
        } else {
          // 若类型是日期
          if (this.extendPropertyVoList[item].propertyType === 3) {
            this.validRules[this.extendPropertyVoList[item].propertyCode] = [
              { validator: this.setValidateForm }
            ];
          } else { // 若类型是字符或数字
            this.validRules[this.extendPropertyVoList[item].propertyCode] = [
              { validator: this.setValidateForm }
            ];
          }
        }
      }
    },
    /**
     * @description: 生成表头扩展字段
     * @return: 无
     * @author: ZJM
     */
    setinitExtend(row) {
      this.tableColumns = [];
      this.tableColumns_head = this.tableColumns_old.slice(0, this.tableColumns_old.length - 1);
      this.tableColumns_operation = this.tableColumns_old[this.tableColumns_old.length - 1];
      this.tableColumns = this.tableColumns.concat(this.tableColumns_head);
      if (row.length > 0) {
        for (var i = 0; i < row.length; i++) {
          if (row[i].propertyType === 1) {
            this.tableColumns.push({
              field: row[i].propertyCode,
              title: row[i].extendName,
              width: '160',
              align: 'center',
              className: i,
              editRender: { name: 'input' }
            });
          } else if (row[i].propertyType === 2) {
            this.tableColumns.push({
              field: row[i].propertyCode,
              title: row[i].extendName,
              width: '160',
              align: 'center',
              className: i,
              editRender: { name: '$input', props: { type: 'number' } }
            });
          } else if (row[i].propertyType === 3) {
            var num = '0';
            if (row[i].propertyLength === 1) {
              num = '220';
            } else if (row[i].propertyLength === 2) {
              num = '160';
            } else if (row[i].propertyLength === 3) {
              num = '160';
            }
            this.tableColumns.push({
              field: row[i].propertyCode,
              title: row[i].extendName,
              width: num,
              align: 'center',
              className: i,
              editRender: { name: 'ElDatePicker', props: { type: 'datetime', format: FORM_CONFIG_DATE[row[i].propertyLength] } }
            });
          }
        }
      }
      this.tableColumns = this.tableColumns.concat(this.tableColumns_operation);
    }
  }
};
</script>
<style lang="less" scoped>
@import "../../../assets/less/equipment/toolNotify/index.less";
</style>
