<!--
 * @Descripttion: 工具提醒
 * @version:
 * @Author: Lois
 -->
<template>
  <el-container style="height: 100%">
    <el-header>
      <header-buttons
        v-if="!toolDetail.show && !addTool.show && !moveToolPage.show && !toolHistory.show"
        ref="headButtons"
        @buttonClickEvent="buttonClickEvent"
        cssType="1"
      ></header-buttons>
      <header-buttons
        v-if="addTool.show"
        ref="headButtonsx"
        @buttonClickEvent="buttonClickEvent"
        showBack=true
        showTitle=true
        :titleName="title"
        cssType="99"
      ></header-buttons>
      <HeaderTitle v-if="toolDetail.show || moveToolPage.show || toolHistory.show" class="header-title" @searchButtonClick="showSearchCard" :flag="!toolDetail.show&&!addTool.show" :titleName="title">
        <div slot="head-operation" class="head-operation">
          <!-- <HeadOperationItem name="addTool" v-if="!addTool.show&&!toolHistory.show&&!toolDetail.show&&!moveToolPage.show&&selectCode" @onOprationItem="showAddToolPage()">
            <i slot="operation-icon" class="icon-batchAdd"></i>
            <span slot="operation-name">{{EQUIPMENT_DATA['ADD_TOOL']}}</span>
          </HeadOperationItem> -->
<!--            <HeadOperationItem name="addDevice"  v-if="!addTool.show&&!toolHistory.show&&!toolDetail.show&&!moveToolPage.show&&selectCode"  @onOprationItem="importTable()">
            <i slot="operation-icon" class="icon-import"></i>
            <span slot="operation-name">{{ BTN_TEXT.IMPORT }}</span>
          </HeadOperationItem> -->
         <!--  <HeadOperationItem name="addTool" v-if="addTool.show" @onOprationItem="addToolItem()">
            <i slot="operation-icon" class="icon-add"></i>
            <span slot="operation-name">{{BTN_TEXT.INSERT_ONE_ROW}}</span>
          </HeadOperationItem> -->
          <HeadOperationItem name="toolpage" @onOprationItem="closeToolHistoryOrDetailPage()">
            <i slot="operation-icon" class="icon-back"></i>
            <span slot="operation-name">{{BTN_TEXT.RETURN}}</span>
          </HeadOperationItem>
        </div>
      </HeaderTitle>
    </el-header>
    <el-main class="view">
      <header-search
         v-if="!toolDetail.show && !addTool.show && !moveToolPage.show && !toolHistory.show"
        :show-primary-search="true"
        @refreshTableData="refreshTableData"
        cssType="1"
      >
      </header-search>
     <!--  <search-box v-show="toolListTable.isSearchBoxShow" @searchBtnClick="complexSearch" :searchList="toolListTable.searchList"></search-box> -->
      <!-- <search-box v-show="toolHistory.isSearchBoxShow" @searchBtnClick="complexSearchHistory" :searchList="toolHistory.searchList"></search-box> -->
      <el-row v-show="!addTool.show&&!toolHistory.show&&!toolDetail.show&&!moveToolPage.show" style="height: calc(100% - 40px)">
         <el-col :xs="5" :sm="5" :md="5" :lg="5" :xl="5" class="tool-area left">
           <div class="tool-content">
            <div class="workshop-tree">
              <HeaderTitle class="header" :titleName="EQUIPMENT_DATA.GROUP_LIST"></HeaderTitle>
              <ZTree :nodes="nodeData" @ztreeOnClick="handleNodeClick" :isExpendAll="true" style="overflow-y: auto;" ></ZTree>
            </div>
           </div>
         </el-col>
         <el-col :xs="19" :sm="19" :md="19" :lg="19" :xl="19" class="tool-area right">
           <div>
             <HeaderTitle class="tool-list-title" :titleName="EQUIPMENT_DATA.CONTAIN_TOOL_LIST + ' '+ selectName ">
              <div slot="head-operation" class="head-operation">
                <HeadOperationItem name="historyData" @onOprationItem="showToolHistoryPage()" v-if="selectCode">
                  <i slot="operation-icon" class="icon-add"></i>
                  <span slot="operation-name">{{EQUIPMENT_DATA.TOOL_HISTORY_DATA}}</span>
                </HeadOperationItem>
                <HeadOperationItem name="showInfo" @onOprationItem="getToolListData(selectCode)">
                  <i slot="operation-icon" class="icon-refresh"></i>
                  <span slot="operation-name">{{BTN_TEXT.REFRESH}}</span>
                </HeadOperationItem>
              </div>
             </HeaderTitle>
            <el-row>
              <el-col :span="24">
                <VXETable
                    v-if="toolListTable"
                    :tableName="'test'"
                    :height="$options.filters.getTableHeight(233)"
                    :tableLoading="tableLoading"
                    :pager-config="toolListTable.page"
                    :tableDatas="toolListTable.data"
                    :tableColumns="myTableColumns"
                    :tablePage="toolListTable.page"
                    :checkRecords="selectRecords"
                    :highlightCurrentRow="true"
                    @sortChange="sortChange"
                    @onTablePageChange="handleToolListTablePageChange"
                    @chooseCheck="checkboxChangeEvent"
                    @clickRows="clickRows"
                    :checkboxConfig="toolListTable.checkboxConfig"
                    ref="toolListGrid"
                  >
                    <div slot="tableOperation" class="tableOperations">
                      <div class="tableOperation-item">
                        <HeadOperationItem name="batchDeleteStaff" @onOprationItem="stopSelectedTool()">
                          <i slot="operation-icon" class="icon-batchDelete"></i>
                          <span slot="operation-name">{{BTN_TEXT.STOP_ALL}}</span>
                        </HeadOperationItem>
                      </div>
                    </div>
                </VXETable>
              </el-col>
            </el-row>
           </div>
         </el-col>
      </el-row>
      <!-- 添加工具部分 -->
      <el-row v-if="addTool.show">
          <addTool
          ref="addTool"
          :code="selectCode"
          :addFlag="addToolItemFlag"
          :handleClose="closeAddToolPage"/>
          <!-- <vxe-grid
            border="full"
            resizable
            keep-source
            ref="adTable"
            class="fix-mg-table"
            :edit-rules="addTool.validRules"
            :columns="addTool.tableColumns"
            :data="addTool.data"
            height="550"
            :edit-config="{trigger: 'click', mode: 'row', showStatus: true, icon: 'fa fa-file-text-o'}"
          ></vxe-grid> -->
      </el-row>
      <el-row v-if="toolHistory.show">
        <toolHistory @goBack="goBack" :code="selectCode" :complexSearch="toolHistory.searchData"></toolHistory>
      </el-row>
      <el-row v-if="toolDetail.show">
        <toolDetail @goBack="goBack" :toolId="selectTool.toolId.toString()"></toolDetail>
      </el-row>
      <el-row v-if="moveToolPage.show">
        <moveTool @goBack="goBack"></moveTool>
      </el-row>
    </el-main>
    <!-- 修改工具modal -->
    <modify-tool-modal
      :showDialog="showModifyTool"
      :resOrigData="selectTool"
      :title="EQUIPMENT_DATA.UPDATE_TOOL_ACTION"
      :handleCancel="clickshowModifyToolCancel"
      :handleConfirm="saveModifyTool"
      :handleClose="clickshowModifyToolCancel"
    />
    <!-- <el-dialog
      v-if="dialogVisibleCustomColumn"
      ref="CustomColumnDialog"
      :class="[$store.state.dialogMarginClass,'CustomColumn']"
      :title="VARIABLE.CUSTOM_COLUMN"
      :width="'600px'"
      :visible.sync="dialogVisibleCustomColumn"
    >
      <CustomColumn
        ref="CustomColumn"
        :nowColumns="toolListTable.columns"
        @saveCustomColumn="saveCustomColumn"
        @showCustomColumn="showCustomColumn"
        @resetCustomColumn="resetCustomColumn"
      ></CustomColumn>
    </el-dialog> -->
    <dialog-setting-column
        :show-setting-dialog.sync="isSettingCustomDialogShow"
        :default-columns="toolListTable.columns"
        :page-columns="myTableColumns"
        @saveMyColumnSetting="handleSaveColumnSetting"
      />
     <!-- 工具导入 -->
     <ImportDialog
          ref="ImportDialog"
          v-if="isImportDialogShow"
          :importDialogName="EQUIPMENT_DATA.IMPORT_TOOLS"
          :exportFileName="EQUIPMENT_DATA.TOOLS_TEMPLATE"
          @exportTemplate="exportTemplate"
          @submitImportFile="submitImportFile"
          @closeImportDialog="closeImportDialog"
        ></ImportDialog>

   <el-dialog
      :class="[$store.state.dialogMarginClass]"
      :title="'工具二维码'"
      :width="'350px'"
      :visible.sync="dialogVisibleQrCode"
    >
      <div id="printDiv" style="text-align: center;" >
        <!-- <el-image :src="userQRcode"></el-image> -->
        <vue-qr :logoSrc="imageUrl" :text="qrCode" :size="200"></vue-qr>
        <div class="printName">{{userName}}</div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" v-print="'#printDiv'">{{BTN_TEXT.PRINT}}</el-button>
        <el-button @click="dialogVisibleQrCode = false">{{BTN_TEXT.CANCEL}}</el-button>
      </span>
    </el-dialog>
  </el-container>
</template>

<script>
import js from '@/assets/js/equipment/ToolNotify/index.js';
export default js;
</script>
<style lang="less" scoped>
@import '../../../assets/less/equipment/toolNotify/index.less';
</style>
