    /**
     * @description: 恢复默认自定义列
     * @param 无
     * @return: void
     * @author: ZJM
     */
    resetCustomColumn() {
      this.showCustomColumn();
      // 按时间从大到小
      this.toolListTable.columns = this.toolListTable.columns.sort((a, b) => {
        return a.zIndex > b.zIndex ? 1 : -1;
      });
      this.toolListTable.columns = this.toolListTable.columns.map((item) => {
        item.width = item.yWidth;
        item.visible = item.yVisible;
        return item;
      });
    },
