    /**
 * @description:导入
 * @param {type}
 * @return:
 * @author: YCB
 */
    submitImportFile(param) {
      const fileObj = param.file;
      const fd = new FormData();
      fd.append('file', fileObj);
      this.$confirm(`您确定要导入到${this.selectName}吗？`, this.GLOBAL.TITLE.PROMPT, {
        dangerouslyUseHTMLString: true,
        confirmButtonText: this.GLOBAL.BTN_TEXT.CONFIRM,
        cancelButtonText: this.GLOBAL.BTN_TEXT.CANCEL,
        type: 'warning'
      }).then(() => {
      // 执行接口
        exportImportToolAction({ deviceCode: this.selectCode }, fd, {
          onUploadProgress: event => {
            // eslint-disable-next-line prefer-const
            let uploadedPercentage = Math.floor(
              (event.loaded * 100) / event.total
            );
            // option.onProgress({ percent: uploadedPercentage });
            this.$refs.ImportDialog.uploadPercent = uploadedPercentage;
          }
        })
          .then(res => {
            if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
              param.onSuccess(res);
              this.getToolListData(this.selectCode);
            } else {
              param.onError(res);
            }
          })
          .catch(err => {
            showWarningMessage(err);
            param.onError(err);
          });
        this.$refs.ImportDialog.isUploadProgressShow = true;
      }).catch(() => {
        this.$refs.ImportDialog.isUploadProgressShow = false;
      });
    },
    // 关闭dialog
    closeImportDialog() {
      this.isImportDialogShow = false;
    },
